<template>
	<div id="app">
		<Header />
		<div id="mainContainer">
			<Slideshow></Slideshow>
			<Nav></Nav>
			<router-view class="main-box"></router-view>
			<SiderInfo />
		</div>
		<Footer />
	</div>
</template>
<script>
import SiderInfo from '@/components/SiderInfo.vue';
import Header from '@/components/Header.vue';
import Nav from '@/components/Nav.vue';
import Footer from '@/components/Footer.vue';
import Slideshow from '@/components/Slideshow.vue';
import { createFingerprint } from '@/util';
import clickSpecialEffect from '@/util/clickSpecialEffect';
// import e from '@/util/bubbleSpecifalEffect';
import './assets/font/font.css';
export default {
	data() {
		return {
			clip: false,
		};
	},
	components: {
		SiderInfo,
		Header,
		Nav,
		Footer,
		Slideshow,
	},
	created() {
		if (!localStorage.getItem('browserId')) {
			createFingerprint();
		}
	},

	mounted() {
		clickSpecialEffect();
		// e();
	},
};
</script>
<style lang="less">
* {
	padding: 0;
	margin: 0;
	font-family: 'JBHGXK';
}
#mainContainer {
	background: url('@/assets/img/bg.png') no-repeat;
	background-size: 100% 100%;
	background-attachment: fixed;
	width: 100%;
	margin-top: 50px;
	box-sizing: border-box;
	padding-left: 10%;
	padding-right: 10%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.main-box {
		flex: 1;
		// background-color: rgba(255, 255, 255, 0.7);
		margin-top: 5px;
		margin-bottom: 10px;
		border-radius: 5px;
		min-height: 100vh;
	}
}

::-webkit-scrollbar {
	/*滚动条整体样式*/
